<template>
  <v-list subheader>
    <v-subheader>Step1. 1/2 ~ 1/8 | 음성지원(~73): 신OO자매님)</v-subheader>

    <template v-for="(item, index) in vocaItems">

      <v-list-item :key="item.title" @click="foo(index)">
        <v-list-item-avatar size="40" tile>
          <!-- <v-img :src="require(`@/assets/image/1.jpg`)"> 1 </v-img> -->
          {{index+1}}.
        </v-list-item-avatar>

        <!-- <v-list-item-content> -->
          <v-list-item-title
            v-if="isWordOn === true"
            v-text="item.title"></v-list-item-title>
          <v-list-item-title
            v-else></v-list-item-title>

          <v-list-item-subtitle 
            v-if="isMeaningOn === true"
            v-text="item.subtitle"></v-list-item-subtitle>
          <v-list-item-subtitle
            v-else></v-list-item-subtitle>
        <!-- </v-list-item-content> -->

        <!-- <v-list-item-action>
          <v-btn icon>
            <v-icon
              color="grey lighten-1"
            >
              mdi-bookmark-outline
            </v-icon>
          </v-btn>
        </v-list-item-action> -->

      </v-list-item>

      <v-divider
        v-if="index < vocaItems.length - 1"
        :key="index"
        inset
      ></v-divider>

    </template>
  </v-list>
</template>

<script>
// import Main from '@/views/Main';

import { mapState } from 'vuex'



export default {
  name: 'Voca',

  components: {
    // Main,
  },

  data: () => ({
    selectedItem: null,
  }),
  methods: {
    foo(offset) {
      const baseIdx = 0;
      try {
        const sound = require(`@/assets/audio/${baseIdx+offset+1}.mp3`);
        let audio = new Audio(sound);
        audio.play();
      } catch (error) {
        // console.error(error);
      }
    }
  },
  computed: {
    ...mapState([
      'isWordOn',
      'isMeaningOn',
      'vocaItems',
    ])
  }
};
</script>
